.abt-container{
    padding: 55px;
    margin: 100px 100px;
    @media screen and (max-width:1000px) {
        margin: 20% 0px;
        font-size: 16px;
        // padding: 10px;
        
    }

.my-texts{
    color: black;
    animation-duration: 2s;
    
    h4{
        text-transform: uppercase;
        font-size: 15px;
        letter-spacing: 5px;
        color: rgb(2, 159, 207);
        font-weight: bold;
        margin-bottom: 35px;


    }
    h3{
        text-transform: uppercase;
        font-size: 23px;
        letter-spacing: 3px;
        margin-top: 5px;
        margin-bottom: 10px;
        font-family: 'Benne', serif;
        margin-bottom: 20px;
        animation-delay: .5s;

    }
    p{
        font-family: 'Nunito', sans-serif;
        animation-delay: .8s;
        animation-duration: 2s;

    }


    }
}


