
.App .sideBar{
    width: 17%;
    height: 100vh;
    background-color: #0f1b53;
    position: fixed;
    margin-left: 30px;
    z-index: 10;
    transform-origin: left;

    @media screen and (max-width:1000px) {
      transform: translateX(-140%);
      margin: 0;
      
    }
  }
  .sideBar.nav-toggle{
    @media screen and (max-width:1000px){
    transform: translate(100%);
        width: 190px;
        right: 100%;
        position: fixed;
        transition: 1s;
  }

}
  
  
  .App .mainContent{
    width: 81%;
    margin-left: 19%;
    min-height: 100vh;
    background-color: #d8e2dc;
    display: grid;
    position: relative;
    @media screen and (max-width:1000px) {
    margin: 0px;
    width: 100%;
 

    }
  
  }

.nav-btn{
  position: absolute;
  top: 5%;
  right: 5%;
  z-index: 10;
  // background-color: red;
  width: 2rem;
  border-radius: 20%;

  display: none;
  align-items: center;
  justify-content: space-evenly;
  cursor: pointer;

  .lines-1, .lines-2, .lines-3{
    height: 2px;
    width: 100%;
    background-color: black;
    display: none;
    pointer-events: none;
    &:not(:last-child){
      margin-bottom: .5rem;
    }
  }
  @media screen and (max-width:1000px) {
    display: block;
  .lines-1 ,.lines-2, .lines-3{
    display: block;
  }
    }
  
}
