.work-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    margin-top: 20px;
    @media screen and (max-width:1000px) {
        position: relative;
        top: 7%;
    }
    hr{
        display: none;
}
    img{
        width: 86%;
        margin: auto;
    }
    .card1,.card2{
        display: grid;
        width: 80%;
        grid-template-columns: 1fr 1fr;
        background: transparent;
        border: none;
        margin-bottom: 2rem;
        animation-duration: 2s;
        @media screen and (max-width:1000px) {
            display: flex;
            flex-direction: column;
            border: 1px dotted black;   
            border-color: transparent;
        }
        &:nth-child(even) img{
            order: 2;
        }
    }
    .card-text{
        text-align: center;
        margin-top: 10px;
        a{
            border: 1px solid black;
            
        }
        h1{
            font-size: 23px;
            letter-spacing: 4px;
            color: black;
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 30px;

        
        }
        li{
            color: #003566;
            font-size: 18px;
            font-family: 400;
            text-align: left;
            line-height: 1.5;
        }
        .btn {
            color: black;
            font-size: 15px;
            font-weight: bold;
            text-transform: uppercase;
            letter-spacing: 2px;
            &:hover{
                border-color: #00b4d8;
                transition: 0.8s;
            }
        
        }
        
    }
    
    .btn-more{
        width: 80%;
        background-color: lightseagreen;
        text-align: center;
        padding: 10px;
        margin-top: -10px;
        a{
            color: black;
            text-decoration: none;
            letter-spacing: 2px;
            font-size: 15px;
            font-weight: bold;
            text-transform: uppercase;
            display: block;
            width: 100%;
        }
    }
}
