.homepage{
    position: absolute;
    top: 15%;
    left: 10%;
    .hero-text{
        font-family: 'Benne', serif;
        margin-bottom: 20px;
        font-size: 60px;
        line-height: 1.3;
        font-weight: 700;
        color: black;
        animation-duration: 2s;
    }
    
    p{
        font-size: 25px;
        color: black;
        font-weight: bold;
        text-shadow: #ffffff 30px 0 30px;
        font-family: 'Nunito', sans-serif;
        animation-duration: 1.5s;
        animation-delay: .2s;
        
    }
    .buttons a{
        color: black;
        padding: 10px;
        background: transparent;
        margin-right: 5px;
        border: 1px solid black;
        margin-top: 8px;
        font-family: 'Nunito', sans-serif;
        font-size: 13px;
        letter-spacing: 2px;
        font-weight: bold;
        text-transform: uppercase;
        cursor: pointer;
        text-decoration: none;
        &:hover{
        border: 1px solid #05bdf5;
        transition: 0.5s;
        @media screen and (max-width:1000px) {
            margin-top: 10px;
        }


        }
    }
    .introgif{
    position: fixed;
    top: 25%;
    right: 110px;
    @media screen and (max-width:1000px) {
        display: none;
    }
    }
}
.header{
    position: fixed;
    top: 25%;
    @media screen and (max-width:1000px) {
        left: 20%;
        }
}

