.maintableedu{
    padding: 55px;
    margin: 100px 100px;
    @media screen and (max-width:1000px) {
        margin: 20% -5%;
    }    



    .edu-title .txt{
        text-transform: uppercase;
        font-size: 15px;
        letter-spacing: 5px;
        margin-bottom: 10px;
        color: #029fcf;
        font-weight: bold;
        animation-duration: 1.5s;
  
    }

    .accordion{
    margin: 25px 0px;
    font-family: 'Nunito', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 13px;
    position: absolute;
    width: 60%;
    animation-duration: 2s;
    animation-delay: .5s;
    animation-fill-mode: both;
    @media screen and (max-width:1000px) {
        width: 80%;
    }

    }
    .card-items{
    list-style: none;
    color: black;
    line-height: 2;
    }
    .card-header{
        color: black;
        font-weight: bold;
        background-color: #0a506545;
        padding: 15px;
    cursor: pointer;
   

        &::before{
        float: right !important;
        font-family: FontAwesome;
        content:"+";
        padding-right: 5px;
        }
        
    }
    p{
    font-size: 15px;
    color: black;
    font-weight: bold;
    @media screen and (max-width:1000px) {
        font-size: 13px;
        letter-spacing: 1px;
            }

    }
    
    ul li{
        font-weight: bold;
    }

}




