.navBar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    .profile{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
    .profile 
    img{
    width: 80%;
    border: 5px solid white;
    border-radius: 50%;
    margin: 58px 27px 10px 18px;
    }

    .nav{
        text-align: center;

    }

    .nav ul li{
        list-style: none;
        padding: 10px;
        text-transform: uppercase;

    }
    .nav ul li a{
        color: white;
        text-decoration: none;
        font-size: 15px;
        display: block;
        position: relative;
        &::before {
        content: ""; 
        position: absolute;
        width: 100%;
        height: 2px;
        bottom: 0;
        left: 3px;
        background-color: yellow;
        visibility: hidden;
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transition: all 0.3s ease-in-out 0s; 
        transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        
    }
      

    &:hover{
        color: yellow;

        &::before {
            visibility: visible;
            -webkit-transform: scaleX(1);
            transform: scaleX(1);
        }   
        }
        .nav ul li a:active{
            color: yellow;
        }
    }
    p{
        color: rgb(250, 212, 0);
        text-transform: uppercase;
        margin-top: 10px;
        font-family: 'Playfair Display', serif;
        letter-spacing: 2px;
    } 
}
NavLink:active{
    color: yellowgreen;
}
