.main-contact-container{
    width: 60%;
    margin: 93px auto;
    @media screen and (max-width:1000px) {
        width: auto;        
    }

    .contact-Container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    animation-duration: 2s;
    @media screen and (max-width:1000px) {
        width: min-content;
        }

    .contact-info{
        text-align: left;
        width: 100%;
        float: left;
        margin-bottom: 40px;
        margin-left: 20%;
        position: relative;
        display: inline-flex;
        .contact-info-text{
            margin-left: 25%;
            padding: 10px;
            a,p{
            color: #0f1b53;
            text-decoration: none;
            font-weight: bolder;
            font-size: 20px;
            
            }

        }
        .contact-info-img img{
            position: absolute;
            top: 0;
            left: 0;
            width: 50px;
        }
    }
    }
    .heading h3{
        text-align: center;
        color: #0f1b53;
        letter-spacing: 2px;
        font-weight: 700;
        font-size: 20px;
        font-family: 'Nunito', sans-serif;
        text-transform: uppercase;
    }

}

.social-frame{
    text-align: center;
    a{
        font-size: 45px;
        padding: 10px;
        text-decoration: none;
        animation-duration: 2s;

    }
    p{
        text-align: center;
        color: #0f1b53;
        letter-spacing: 2px;
        font-weight: 700;
        font-size: 20px;
        font-family: 'Nunito', sans-serif;
        text-transform: uppercase;
    }
}
