.techstacks{
    margin: 50px 9%;
    position: fixed;
    @media screen and (max-width:1000px) {
      position: relative;
      margin-top: 20%;
    }
    
    p{
        text-align: center;
        font-size: 15px;
        margin-top: 20px;
        text-transform: uppercase;
        color: #0f1b53;
        letter-spacing: 1px;
        font-weight: bold;
        animation-duration: 1s;
    
    }
    .single-tech{
    display: table-cell;
    text-align: center;
    position: relative;
    // border: 2px solid black;
    @media screen and (max-width:1000px) {
      display: block;
    }
        
        img{
            // border: 2px solid black;
            width: 100px;
            margin: 5px 30px;
            display: block;
            text-align: center;
            cursor: pointer;
    

        }
        .img-title{
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        margin-top: 14px;
        font-size: 15px;
        font-weight: bold;
        letter-spacing: .5px;
        text-transform: uppercase;
        color: rgb(3, 70, 146);
        }
    }
    .tocenter{
        margin: 10px 21%;
        animation-duration: 1.5s;
    }
    .major{
        margin: 10px 5%;
        animation-duration: 1.5s;
    @media screen and (max-width:1000px){
      margin: 10px 21%;

    }
        


    }
}

  .image {
    display: block;
    height: auto;
    width: 100px;
    margin: 5px 40px;
    display: block;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    bottom: 30px;
    left: 26px;
    right: 0;
    width: 70%;
    opacity: 0;
    transition: 1s cubic-bezier(0.46,-0.2, 0.25, 1);
    background-color: #3a86ff;
    border-radius: 10px;
    cursor: pointer;

  }
  
  .single-tech:hover .overlay {
    opacity: 1;
  }
  
  .text {
    color: white;
    font-size: 25px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
  }
